<template>
  <div id="feedbackpage">
    <div class="top_area">
    <!-- Header Area that holds a Top, Left and Right Nav area as well as a Center Widget Area - width: 100%, height: 12.5% (480px), z-index: 1 -->
    </div> <!-- End .top_area -->
    <div class="main_area">
        <div class="feedback">
        <h1 class="prompt">
            <img src="./media/give-us-your-feedback.gif"/>
        </h1>

            <div class="option" id="neutral">
                <img src="./media/feedback emojis/Neutral.gif"/>
            </div>
            <div class="option" id="happy">
                <img src="./media/feedback emojis/Happy.gif"/>

            </div>
            <div class="option" id="love">
                <img src="./media/feedback emojis/Love.gif"/>
            </div>
            <div class="option" id="wow">
                <img src="./media/feedback emojis/Wow.gif"/>
            </div>
        </div>
        <div class="modal">
            <transition appear name="modal" :key="modalVid">
                <video autoplay playsinline :muted="isMuted" ref="modalVideo" :key="modalVid">
                    <source :src="modalVid" type="video/mp4" />
                </video>
            </transition>
        </div>
    </div>

 

  </div>
</template>

<script>
  export default {
    name: "FeedbackPage",
    components: {
    },
    methods: {
        randomizeVideo(num) {
            this.modalVid = require(`./media/feedback action videos/${this.dir[num]}`);
            this.$refs.modalVideo.removeEventListener("ended", () => this.handleEnded(num));
            this.$nextTick(() => {
                this.$refs.modalVideo.addEventListener("ended", () => this.handleEnded(num));
                this.$refs.modalVideo.play();
            })

        },
        integer() {
            if (this.randomized) {
                var num = Math.floor(Math.random() * this.dir.length);
                while (this.videosPlayed.indexOf(num) > -1) {
                    num = Math.floor(Math.random() * this.dir.length);
                }
                return num;
            } else {
                this.counter++
                return this.counter;
            }
            
        },
        handleEnded(num) {
            this.videosPlayed.push(num);
            // if (this.videosPlayed.length === this.dir.length) {
            if (this.videosPlayed.length === 1) {
                if (this.loopAll) {
                    this.videosPlayed = [];
                } else {
                    console.log("do timer");
                    this.timeout = setTimeout(() => {
                        window.location.href = "./index.html";
                        clearTimeout(this.timeout);
                    }, 30000);
                    return;
                }
            }
            this.$nextTick(() => {
                this.randomizeVideo(this.integer());
            })
        },
        handleClick(e) {
            if (e.target.className === "option") {
            // Log this nalytics: what video and the chosen
            // this.$store.commit("setFeedback", []);
                let feedbackArr = [...this.$store.state.feedback];
                if (feedbackArr.map(i => i.video).indexOf(window.location.href.split("#video=")[1]) > -1) {
                    if (feedbackArr.filter(i => i.video === window.location.href.split("#video=")[1])[0].response[e.target.id]) {
                        feedbackArr.filter(i => i.video === window.location.href.split("#video=")[1])[0].response[e.target.id]++
                    } else {
                        feedbackArr.filter(i => i.video === window.location.href.split("#video=")[1])[0].response[e.target.id] = 1;
                    }
                    
                    // console.log(feedbackArr.filter(i => i.video === window.location.href.split("#video=")[1])[0].response);
                } else {
                    feedbackArr.push(
                        {
                            video: window.location.href.split("#video=")[1],
                            response: {
                                [e.target.id]: 1
                            }
                        }
                    )
                }
                this.$store.commit("setFeedback", feedbackArr);
                // console.log(this.$store.state.feedback);
                window.location.href = "./index.html";
            }
        }
    },
    data() {
      return {
        // this feedback page should play one video only
        dir: require.context('./media/feedback action videos', false, /\.mp4|.png$/).keys().length > 0 ? 
            require.context('./media/feedback action videos', false, /\.mp4|.png$/).keys().map((url) => url.split("./")[1]) : null,
        isMuted: false,
        videosPlayed:[],
        modalVid:null,
        loopAll:false,
        randomized:true,
        counter: -1,
        timeout: null
      };
    },
    mounted() {

        try {
            this.$cms.filter(i => i.fields.ID === window.location.href.split("#video=")[1])[0].fields.Feedback_Video[0].url;
            this.dir = [];
            this.dir = this.$cms.filter(i => i.fields.ID === window.location.href.split("#video=")[1])[0].fields.Feedback_Video;

        } catch(err) {
            /* vue-disable-eslint no-empty */
            // window.location.href = "./index.html";
        }
        this.randomizeVideo(this.integer());
        document.getElementById("feedbackpage").addEventListener("click", this.handleClick);
        // window.addEventListener("click", () => {this.$refs.modalVideo.play();})
    }
  };
</script>

